  created() {
    this.$nextTick(() => {
      this.initCharts();
    });
  },
  methods: {
    initCharts() {
      let _this = this;
      this.myChart = echarts.init(document.getElementById("main"));
      echarts.registerMap("luoyang", mapData);

      this.myChart.on("click", function (params) {
        if (!_this.routes.includes(params.region.id)) {
          _this.routes.push(params.region.id);
        } else {
          let idx = _this.routes.indexOf(params.region.id);
          _this.routes.splice(idx, 1);
        }
      });

      this.myChart.setOption(this.option);
    },
    btn() {
      if (this.routes.length === 2) {
        this.path = { start: this.routes[0], end: this.routes[1] };
      } else {
        this.path = {
          start: this.routes[0],
          location: this.routes.slice(1, this.routes.length - 1),
          end: this.routes[this.routes.length - 1],
        };
      }
      this.GetPath(this.path);
    },
    async AddEdge() {
      for (let i = 0; i < 68; i++) {
        const res = await addEdge(this.edges[i]);
      }
    },
    async AddPoint() {
      for (let i = 0; i < 56; i++) {
        const res = await addPoint({ name: this.points[i] });
      }
    },
    async ShowEdge() {
      const res = await showEdge();
      console.log(res);
    },
    async ShowPoints() {
      const res = await showPoints();
      console.log(res);
    },
    async DeleteEdges() {
      for (let i = 1; i <= 42; i++) {
        const res = await deleteEdges(i);
        console.log(res);
      }
    },
    async GetPath(params) {
      this.option.series[0].data[0].coords = [];
      let res = await getPath(params);
      for (let i = 0; i < res.data.Points.length; i++) {
        this.option.series[0].data[0].coords.push(
          this.coord[res.data.Points[i] - 1]
        );
      }
      this.myChart.setOption(this.option);
    },
  },
};
