//当前这个模块：api进行统一管理
import requests from './require';

export const addEdge = (params) => requests({ url: "/add/edge", method: "post", data: params })

export const addPoint = (params) => requests({ url: "/add/point", method: "post", data: params })

export const showEdge = () => requests({ url: "/show/edges", method: "get" })


export const showPoints = () => requests({ url: "/show/points", method: "get" })

export const deletePoints = (id) => requests({ url: `/delete/point/${id}`, method: "delete" })

export const deleteEdges = (id) => requests({ url: `/delete/edge/${id}`, method: "delete" })

export const getPath = (params) => requests({ url: '/path', method: 'post', data: params })